/****************************************************************************/
/* MAIN STYLESHEET */
/****************************************************************************/

.svg-ico {
	display: inline-block;
	vertical-align: middle;
	fill: currentColor;
	stroke: currentColor;
}

.svg-ico_no-fill {
	fill: none !important;
}

.svg-ico_no-stroke {
	stroke: none !important;
}


/**
* login block
*/

.login-block {
	width: 320px;
	height: 320px;
	display: inline-block;
	border: 8px solid #fff;
	border-radius: 16px;
	text-decoration: none;

	&:active {
		opacity: 0.9;
	}
}

.login-block_error {
	border-color: #D11F1F;
}

.login-block_success {
	border-color: #14CC14;
}

.login-block_authorized {
	background: #fff;
	padding: 16px;
	color: #000;
	overflow: hidden;
}

.login-block_transparent {
	border-width: 0;
	position: relative;

	.login-block__result {
		position: absolute;
		bottom: 100%;
		margin-bottom: 12px;
	}
}

.login-block__result {
	height: 48px;
}

.login-block__thumb {
	height: 104px;
	width: 104px;
	margin: 0 auto 24px;
	position: relative;
	overflow: hidden;
}

.login-block__shortname {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
}

.login-block__img {
	display: block;
	height: 100%;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	border-radius: 50%;
}

.login-block__title {
	font-weight: bold;
	font-size: 28px;
	line-height: 32px;
	letter-spacing: -.3px;
}


/**
* content
*/

.content__col {
	// padding: 8px 10px;
	// padding: 0 10px;
	flex: 0 0 auto;
	max-width: 100%;

	& + & {
		border-left: 1px solid rgba(0, 0, 0, 0.1);
	}

	&.content__opened {
		border-left: 0;
	}
	// ajax-обертка битрикса
	& > [id^="comp_"] {
		height: 100%;
	}
}

.content__aside {
	width: 328px;
	padding: 0;
}

.content__kitchen {
	// flex-basis: 0;
	flex-basis: 266px;
}

.content__aside-kitchen {
	padding-left: 60px; // ширина боковой панели
}

.content__opened,
.content__checkout {
	width: 268px;
}

.content__filler {
	display: none;
	flex: 1 1 0%;
	min-width: 0;
}

@media (min-width: 1200px) {

	.content__filler {
		display: block;
	}
}

.content__nav {
	width: 160px;
	margin-left: auto;
	padding-top: 8px;
	padding-bottom: 8px;
	padding-left: 10px;
	padding-right: 10px;
	position: relative;
	z-index: 2;
}

/**
* inline list
*/
// Kitchen $48 • Bar $9 • Service $5
.inline-list {
	display: flex;
	flex-wrap: wrap;
}

.inline-list__item {
	flex: 0 0 auto;
	max-width: 100%;

	& + &:before {
		content: ' • ';
		display: inline-block;
		margin: 0 3px;
		// vertical-align: middle;
	}
}

.order-btn {
	height: 40px;
	line-height: 38px;
	font-weight: 600;
}

.c-btn_default.order-btn_success {
	background-color: $color-success;
	border-color: $color-success;
}

.c-btn_default.order-btn_error {
	background-color: $color-error;
	border-color: $color-error;
}

// counter

.c-sup {
	color: rgba(36, 39, 46, 0.5);
	position: absolute;
	top: 0;
	margin-left: 2px;
}


/**
* Menu slider
*/

.keyboard .menu-slider {
	height: 100%;
	max-width: none;
	margin-left: -20px;
	margin-right: -20px;
}

.menu-slider {
	max-width: 100%;
	position: relative;
}

.menu-slider__main {
	width: calc(100% - 86px);
	height: 100%;
	overflow: hidden;
	padding-left: 20px;
	// padding-right: 20px;
	padding-right: 0;
	border-left: 1px solid rgba(255, 255, 255, 0.1);

	.slick-list,
	.slick-track {
		height: 100%;
	}

	.slick-slide {
		overflow-y: auto;
		padding-right: 20px;

		&.disable-overflow {

			.simplebar-content {
				height: 100%;
				overflow: hidden;
			}
		}
	}

	.slick-slide > div {
		height: 100%;
		// padding-right: 20px;
		// overflow-y: auto;
	}
}

.menu-slider__panel {
	overflow: hidden;
	// height: 100%;
}

.menu-slider__aside {
	width: 86px;
	height: 100%;
	overflow: hidden;
	padding-left: 20px;
	padding-right: 20px;
}

.controls-list {
	margin-left: -6px;
	margin-right: -6px;
}

.controls-list__item {
	padding-left: 6px;
	padding-right: 6px;
}

.account-card {

}

.account-card__thumb {
	width: 72px;
	height: 72px;
	border-radius: 50%;
	overflow: hidden;
	margin-bottom: 12px;
	text-align: center;
	line-height: 72px;
	position: relative;
}

.account-card__img {
	display: block;
	height: 100%;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	background-color: rgba(37,40,44,.1);
}

.account-card__shortname {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	line-height: inherit;
}

.account-card__title {
	margin-bottom: 3px;
}

.c-panel {

	.account-card,
	.shift-details {
		padding-left: 12px;
		padding-right: 12px;
	}
}

.buttons-menu {
	// box-shadow: 0px 30px 60px rgba(37, 40, 44, 0.2), 0px 2px 4px rgba(37, 40, 44, 0.02);
	// backdrop-filter: blur(120px);
	// border-radius: 10px;
	// overflow: hidden;
}

.buttons-menu__item {

	& + & {
		margin-top: 8px;
	}
}

.buttons-menu__link {
	border-radius: 10px;
	font-size: 17px;
	font-weight: bold;
	height: 54px;
	line-height: 52px;

	.c-btn__icon {
		width: 24px;
		height: 24px;
		margin-right: 5px;
	}
}

.popup-notifications {
	position: absolute;
	bottom: 8px;
	left: 0;
	right: 0;

	.notice + .notice {
		margin-top: 8px;
	}
}

.account-menu {

}

.account-menu__item {
	padding-left: 12px;
	padding-right: 12px;

	&:first-child {
		border-top: 0.5px solid rgba(0, 0, 0, 0.1);
	}

	.account-menu__link {
		border-bottom: 0.5px solid rgba(0, 0, 0, 0.1);
	}
}

.account-menu__link {
	display: block;
	text-decoration: none;
	color: $text-color;
	position: relative;
	padding: 0 30px 0 12px;
	height: 58px;
	line-height: 58px;
}

.account-menu__icon {
	width: 10px;
	height: 10px;
	position: absolute;
	right: 20px;
	top: 50%;
	margin-top: -5px;
}

.c-panel .account-menu {
	margin-left: -12px;
	margin-right: -12px;
}
