/****************************************************************************/
/* BASIC FORM INPUTS */
/****************************************************************************/

$border-width: 1px;


@mixin status ($color, $percent) {
	background-color: lighten($color, $percent);
	border-color: $color;
}

.c-input {
	display: block;
	width: 100%;
	outline: 0;

	@include input-placeholder {
		color: #8b8a89;
	}
}


/**
* Default
*/

.c-input_default {
	border: $border-width solid #d4d5d8;
	background: #fff;

	@include g-transition(background-color, border-color);

	&:focus {
		border-color: $brand-color;
	}

	&_error {
		@include status($color-error, 30%);
	}

	&_success {
		@include status($color-success, 30%);
	}
}


/**
* Borderless
*/

.c-input_no-border {
	border-color: transparent !important;

	&:focus {
		border-color: transparent !important;
	}
}


/**
* Text
*/

.c-input_text {
	background: none !important;
	padding: 0 !important;
	font-size: inherit !important;
	text-align: inherit !important;
	color: inherit !important;
}


/**
* Multiline input
*/

.c-input_multiline {

	&.c-input_xs {
		height: 70px;
		padding: 10px;
	}

	&.c-input_sm {
		height: 85px;
		padding: 10px;
	}

	&.c-input_md {
		height: 100px;
		padding: 10px;
	}

	&.c-input_lg {
		height: 115px;
		padding: 10px 20px;
	}
}


/**
* Extra small
*/

.c-input_xs {
	height: 24px;
	font-size: 14px;
	padding: 0 10px;
}


/**
* Small
*/

.c-input_sm {
	height: 30px;
	font-size: 14px;
	padding: 0 10px;
}


/**
* Medium
*/

.c-input_md {
	height: 34px;
	font-size: $global-font-size;
	padding: 0 10px;
}


/* Large */

.c-input_lg {
	height: 44px;
	font-size: 18px;
	padding: 0 20px;
}


/**
* Label
*/

.c-input-label {
	display: inline-block;
	color: $text-color;
	font-weight: normal;
	margin-bottom: 8px;
}


/**
* Hidden
*/

.c-input_phone {
	@include hide;
}

/**
* Input groups
*/

.c-input-group {
	position: relative;
	white-space: nowrap;

	display: flex;
	flex-wrap: nowrap;
	width: 100%;
	align-items: stretch;
}

.c-input-group__input {
	flex: 1 1 auto;
	width: 1%;
}
