/****************************************************************************/
/* COLOR */
/****************************************************************************/

@mixin color-class ($palette) {

	@each $modifier, $color in $palette {

		.u-color-#{$modifier} {
			color: $color !important;
		}
	}
}

$palette: (
  ('text', $text-color),
  ('success', $color-success),
  ('error', $color-error),
  ('active-gray', $color-active-gray)
);

@include color-class($palette);
