/****************************************************************************/
/* PAGE LAYOUT */
/****************************************************************************/

/**
* 1. Flexbox bug: min-height breaks layout in IE10-11.
* 2. Prevent text zoom on non-responsive websites in mobile browsers.
*/

.c-page {
	font-family: $brand-font;
	min-width: $phone-width;
	height: 100vh; /* [1] */
	display: flex;
	flex-direction: column;
	background: #F4F5F5;
	overflow: hidden;
}

.content {
	flex: 1 0 auto;
	// background-image: linear-gradient(180deg, #AAB1B8 0%, #B1B6BD 7.81%, #BABCC2 15.1%, #C3C4C7 22.92%, #C3C4C7 30.21%, #C2C3C7 37.5%, #C9C8CC 45.31%, #C8C8CC 53.13%, #C9C8CC 60.94%, #C3C3C7 68.75%, #C1C1CC 76.56%, #C0C1CC 84.37%, #C1C1CC 92.19%, #C1C1CC 100%);
	background: #E8E9EB;
	// height: 100vh;

	position: relative;
}

.content__main {
	height: 100vh;

	display: flex;
	position: relative;
	overflow: hidden;
}

.content__body {
	position: relative;
	// overflow: hidden;

	&.c-overlay {

	}
}

.content__details {
	padding: 16px;

	> .u-row {
		margin-left: -16px;
		margin-right: -16px;
		height: 100%;
	}
}

.content__details-inner {
	padding-left: 16px;
	padding-right: 0;
	// flex-grow: 1;
	height: 100%;
}

.content__details-controls {
	padding-left: 16px;
	padding-right: 16px;
	padding-top: 20px;
}

.content__nav {
	overflow-y: scroll;

	@include hide-scrollbar;
}

.c-overlay {

	@include layer-mask($index: 99, $click-through: false);
}

.c-overlay__highlight {
	position: relative;
	z-index: 100;
}

.content_index {

	.c-container {
		max-width: 100%;
	}
}
