
.c-form__desc {
	margin-bottom: 20px;
}

.c-form__field {

	& + & {
		margin-top: 15px;
	}
}

.c-form__controls {
	margin-top: 30px;
}

.c-form__success {
	display: none;

	position: absolute;
	left: 0;
	right: 0;
	text-align: center;

	@include center-t('ver');
}

.c-form__success-text {
	font-size: 18px;
	text-transform: uppercase;
	font-weight: bold;
	padding: 5px;
	color: $color-success;
}
