/**
* For overriding vendor styles only
*/

/**
* Fancybox 3
*/

.fancybox-slide {
	background-color: rgba(0,0,0,0.5);
}


/**
* Slick Slider
*/

.slick-slide {
	outline: 0;
	will-change: transform;
}

.slick-track,
.slick-list {
	backface-visibility: hidden;
}


