.simplebar-scrollbar {

	&:before {
		left: 0 !important;
		right: 0 !important;
		background: $brand-color;
		opacity: 1 !important;
	}
}

[data-simplebar-thin] {

	.simplebar-track {
		border-radius: 0;
		border-right: 1px solid rgba(255, 255, 255, 0.1);

		&.simplebar-vertical {
			width: 3px; // 2px + border-width

			.simplebar-scrollbar {

				&:before {
					top: 0;
					bottom: 0;
				}
			}
		}
	}

	.simplebar-scrollbar {

		&:before {
			background-color: #86898F;
		}
	}
}
