/****************************************************************************/
/* WIDTHS */
/****************************************************************************/


/**
* Generate grid classes
*/

$fractions: 1 2 3 4 5 !default;

$widths-delimiter: '-' !default;

$widths-breakpoint-separator: \_ !default;

@mixin widths($columns, $breakpoint: null) {

    @each $denominator in $columns {
        @for $numerator from 1 through $denominator {
			@if ($breakpoint == null) {
				.u-col-#{$numerator}#{$widths-delimiter}#{$denominator} {
					width: ($numerator / $denominator) * 100% !important;
				}
			} @else {
				.u-col-#{$numerator}#{$widths-delimiter}#{$denominator}#{$widths-breakpoint-separator}#{$breakpoint} {
					width: ($numerator / $denominator) * 100% !important;
				}
			}
        }
    }
}
