/****************************************************************************/
/* BUTTONS */
/****************************************************************************/

$btn-border-width: 1px;
$buttonsDefault: (
	primary: (
		background-color: $color-action,
		border-color: $color-action,
		color: #fff,
		background-color-hover: $color-action,
		border-color-hover: $color-action,
		color-hover: #fff
	),
	secondary: (
		background-color: rgba(37, 40, 44, 0.05),
		border-color: transparent,
		color: #24272E,
		background-color-hover: rgba(37, 40, 44, 0.05),
		border-color-hover: transparent,
		color-hover: #24272E
	),
	tertiary: (
		background-color: rgba(36, 39, 46, 0.5),
		border-color: transparent,
		color: #fff,
		background-color-hover: rgba(36, 39, 46, 0.5),
		border-color-hover: transparent,
		color-hover: #fff
	),
	success: (
		background-color: $color-success !important,
		border-color: $color-success !important,
		color: #fff !important,
		background-color-hover: $color-success,
		border-color-hover: $color-success,
		color-hover: #fff
	),
	error: (
		background-color: $color-error !important,
		border-color: $color-error !important,
		color: #fff !important,
		background-color-hover: $color-error,
		border-color-hover: $color-error,
		color-hover: #fff
	)
);

$buttonsGhost: (
	primary: (
		background-color: transparent,
		border-color: $color-action,
		color: $color-action,
		background-color-hover: $color-action,
		border-color-hover: $color-action,
		color-hover: #fff
	),
);

$buttonsLink: (
	primary: (
		color: $color-action,
		color-hover: #000
	),
	text: (
		color: $text-color,
		color-hover: #000
	),
	error: (
		color: $color-error,
		color-hover: #000
	),
	light: (
		color: #fff,
		color-hover: #fff
	)
);

.c-btn {
	display: inline-block;
	vertical-align: middle;
	cursor: pointer;
	text-align: center;
	max-width: 100%;
	padding: 0;
	border: 0;
	outline: 0;

	display: inline-flex;
	align-items: center;
	justify-content: center;
}


/**
* Disabled state
*/

.c-btn_disabled {
	cursor: default;
	opacity: 0.3;
}


/**
* Fullwidth
*/

.c-btn_full {
	width: 100%;
}


/**
* Small
*/

.c-btn_sm {
	font-size: 12px;
	height: 28px;
	line-height: 28px - $btn-border-width * 2;
	padding: 0 8px;
	min-width: 70px;

	.c-btn__icon {
		width: 12px;
		height: 12px;
		margin-right: 2px;
	}
}

/**
* Medium
*/

.c-btn_md {
	font-size: 15px;
	height: 36px;
	line-height: 36px - $btn-border-width * 2;
	padding: 0 10px;
	min-width: 96px;
}


/**
* Large
*/

.c-btn_lg {
	font-size: 15px;
	height: 44px;
	line-height: 44px - $btn-border-width * 2;
	padding: 0 10px;
}


/****************************************************************************/
/* DEFAULT BUTTONS */
/****************************************************************************/

@include default-buttons($buttonsDefault, $btn-border-width, 10%);


/****************************************************************************/
/* GHOST BUTTONS */
/****************************************************************************/

@include ghost-buttons($buttonsGhost, $btn-border-width, 10%);


/****************************************************************************/
/* LINK BUTTONS */
/****************************************************************************/

@include link-buttons($buttonsLink);

.c-btn_link.c-btn_sm {
	font-size: 10px;
	line-height: 14px;
	letter-spacing: .1px;
}

.c-btn_link.c-btn_active {
	color: $text-color;
}

/**
* Rounded
*/

.c-btn_rounded {
	border-radius: 100px;
}

.c-btn_order {
	border-radius: 10px;
	text-transform: uppercase;
	height: 40px;
	line-height: 38px;
	font-size: 10px;
	font-weight: 600;
}
