/****************************************************************************/
/* SELECT */
/****************************************************************************/

$btn-width: 25px;

.c-select {
	position: relative;
	display: inline-block;
}

.c-select__btn {
	width: $btn-width;
	position: absolute;
	z-index: 1;
	right: 0;
	top: 0;
	bottom: 0;
	pointer-events: none;

	background: $brand-color;
}

.c-select__icon {
	fill: #fff;
	width: 100%;
	height: 100%;
	padding: 11px;

	@include center-a;
}

.c-select__control {
    appearance: none;
	outline: 0;
	width: 100%;

    &::-ms-expand {
        display: none;
	}
}

/**
* Single
*/

.c-select_single {

	.c-select__control {
		padding-right: $btn-width + 20px;
	}

	&.c-select_md {

		.c-select__btn {
			width: 34px; // input md height
		}
	}
}

.c-select_multiple {

	.c-select__btn {
		display: none;
	}
}
