/****************************************************************************/
/**
 * Include fonts with `@include font($name, $filename, $style: normal, $weight: normal)`
 */
@font-face {
  font-family: "Inter";
  src: url("fonts/Inter-Regular.eot");
  src: url("fonts/Inter-Regular.eot?#iefix") format("embedded-opentype"), url("fonts/Inter-Regular.woff2") format("woff2"), url("fonts/Inter-Regular.woff") format("woff"), url("fonts/Inter-Regular.ttf") format("truetype");
  font-style: 400;
  font-weight: 400;
}

@font-face {
  font-family: "Inter";
  src: url("fonts/Inter-Medium.eot");
  src: url("fonts/Inter-Medium.eot?#iefix") format("embedded-opentype"), url("fonts/Inter-Medium.woff2") format("woff2"), url("fonts/Inter-Medium.woff") format("woff"), url("fonts/Inter-Medium.ttf") format("truetype");
  font-style: 400;
  font-weight: 500;
}

@font-face {
  font-family: "Inter";
  src: url("fonts/Inter-SemiBold.eot");
  src: url("fonts/Inter-SemiBold.eot?#iefix") format("embedded-opentype"), url("fonts/Inter-SemiBold.woff2") format("woff2"), url("fonts/Inter-SemiBold.woff") format("woff"), url("fonts/Inter-SemiBold.ttf") format("truetype");
  font-style: 400;
  font-weight: 600;
}

@font-face {
  font-family: "Inter";
  src: url("fonts/Inter-Bold.eot");
  src: url("fonts/Inter-Bold.eot?#iefix") format("embedded-opentype"), url("fonts/Inter-Bold.woff2") format("woff2"), url("fonts/Inter-Bold.woff") format("woff"), url("fonts/Inter-Bold.ttf") format("truetype");
  font-style: 400;
  font-weight: 700;
}

a {
  color: #ccc;
}

a:hover, a:focus {
  text-decoration: none;
}

p {
  margin-bottom: 20px;
}

p:last-child {
  margin-bottom: 0;
}

/****************************************************************************/
/* LAYOUT */
/****************************************************************************/
.u-row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.u-col {
  padding-left: 15px;
  padding-right: 15px;
  flex: 0 0 auto;
  max-width: 100%;
}

/**
* LAYOUT
*/
/****************************************************************************/
/* PAGE LAYOUT */
/****************************************************************************/
/**
* 1. Flexbox bug: min-height breaks layout in IE10-11.
* 2. Prevent text zoom on non-responsive websites in mobile browsers.
*/
.c-page {
  font-family: "Inter", sans-serif;
  min-width: 320px;
  height: 100vh;
  /* [1] */
  display: flex;
  flex-direction: column;
  background: #F4F5F5;
  overflow: hidden;
}

.content {
  flex: 1 0 auto;
  background: #E8E9EB;
  position: relative;
}

.content__main {
  height: 100vh;
  display: flex;
  position: relative;
  overflow: hidden;
}

.content__body {
  position: relative;
}

.content__details {
  padding: 16px;
}

.content__details > .u-row {
  margin-left: -16px;
  margin-right: -16px;
  height: 100%;
}

.content__details-inner {
  padding-left: 16px;
  padding-right: 0;
  height: 100%;
}

.content__details-controls {
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 20px;
}

.content__nav {
  overflow-y: scroll;
  /* Hide scrollbar for Chrome, Safari and Opera */
  /* Hide scrollbar for IE, Edge and Firefox */
}

.content__nav::-webkit-scrollbar {
  display: none;
}

.content__nav {
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}

.c-overlay {
  position: relative;
}

.c-overlay:after {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.4);
  z-index: 99;
  top: 0;
  left: 0;
  transition: opacity 0.3s;
}

.c-overlay__highlight {
  position: relative;
  z-index: 100;
}

.content_index .c-container {
  max-width: 100%;
}

.s-header {
  flex-shrink: 0;
  background: #ccc;
}

.s-footer {
  flex-shrink: 0;
  background: #ddd;
}

.s-footer .c-container {
  height: 100%;
  position: relative;
}

/**
* COMPONENTS
*/
/****************************************************************************/
/* BUTTONS */
/****************************************************************************/
.c-btn {
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  text-align: center;
  max-width: 100%;
  padding: 0;
  border: 0;
  outline: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

/**
* Disabled state
*/
.c-btn_disabled {
  cursor: default;
  opacity: 0.3;
}

/**
* Fullwidth
*/
.c-btn_full {
  width: 100%;
}

/**
* Small
*/
.c-btn_sm {
  font-size: 12px;
  height: 28px;
  line-height: 26px;
  padding: 0 8px;
  min-width: 70px;
}

.c-btn_sm .c-btn__icon {
  width: 12px;
  height: 12px;
  margin-right: 2px;
}

/**
* Medium
*/
.c-btn_md {
  font-size: 15px;
  height: 36px;
  line-height: 34px;
  padding: 0 10px;
  min-width: 96px;
}

/**
* Large
*/
.c-btn_lg {
  font-size: 15px;
  height: 44px;
  line-height: 42px;
  padding: 0 10px;
}

/****************************************************************************/
/* DEFAULT BUTTONS */
/****************************************************************************/
.c-btn_default {
  text-decoration: none;
  border: 1px solid;
  border-radius: 10px;
  transition: background-color 0.3s, border-color 0.3s;
}

.c-btn_default.c-btn_primary {
  background-color: #1B5CE0;
  border-color: #1B5CE0;
  color: #fff;
}

.c-btn_default.c-btn_secondary {
  background-color: rgba(37, 40, 44, 0.05);
  border-color: transparent;
  color: #24272E;
}

.c-btn_default.c-btn_tertiary {
  background-color: rgba(36, 39, 46, 0.5);
  border-color: transparent;
  color: #fff;
}

.c-btn_default.c-btn_success {
  background-color: #44B25C !important;
  border-color: #44B25C !important;
  color: #fff !important;
}

.c-btn_default.c-btn_success:hover, .c-btn_default.c-btn_success:focus {
  background-color: #44B25C;
  border-color: #44B25C;
  color: #fff;
}

.c-btn_default.c-btn_error {
  background-color: #C3200A !important;
  border-color: #C3200A !important;
  color: #fff !important;
}

.c-btn_default.c-btn_error:hover, .c-btn_default.c-btn_error:focus {
  background-color: #C3200A;
  border-color: #C3200A;
  color: #fff;
}

/****************************************************************************/
/* GHOST BUTTONS */
/****************************************************************************/
.c-btn_ghost {
  text-decoration: none;
  border: 1px solid;
  border-radius: 10px;
  transition: background-color 0.3s, border-color 0.3s;
}

.c-btn_ghost.c-btn_primary {
  background-color: transparent;
  border-color: #1B5CE0;
  color: #1B5CE0;
}

.c-btn_ghost.c-btn_primary:hover, .c-btn_ghost.c-btn_primary:focus, .c-btn_ghost.c-btn_primary.c-btn_pressed {
  background-color: #1B5CE0;
  color: #fff;
}

/****************************************************************************/
/* LINK BUTTONS */
/****************************************************************************/
.c-btn_link {
  border: 0;
  padding: 0;
  box-shadow: none;
  background: none;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 600;
  height: auto;
  line-height: inherit;
  min-width: auto;
}

.c-btn_link.c-btn_primary {
  color: #1B5CE0;
}

.c-btn_link.c-btn_primary:hover, .c-btn_link.c-btn_primary:focus {
  color: #000;
}

.c-btn_link.c-btn_text {
  color: #24272E;
}

.c-btn_link.c-btn_text:hover, .c-btn_link.c-btn_text:focus {
  color: #000;
}

.c-btn_link.c-btn_error {
  color: #C3200A;
}

.c-btn_link.c-btn_error:hover, .c-btn_link.c-btn_error:focus {
  color: #000;
}

.c-btn_link.c-btn_light {
  color: #fff;
}

.c-btn_link.c-btn_sm {
  font-size: 10px;
  line-height: 14px;
  letter-spacing: .1px;
}

.c-btn_link.c-btn_active {
  color: #24272E;
}

/**
* Rounded
*/
.c-btn_rounded {
  border-radius: 100px;
}

.c-btn_order {
  border-radius: 10px;
  text-transform: uppercase;
  height: 40px;
  line-height: 38px;
  font-size: 10px;
  font-weight: 600;
}

/****************************************************************************/
/* CONTAINER */
/****************************************************************************/
.c-container {
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
}

.c-form__desc {
  margin-bottom: 20px;
}

.c-form__field + .c-form__field {
  margin-top: 15px;
}

.c-form__controls {
  margin-top: 30px;
}

.c-form__success {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.c-form__success-text {
  font-size: 18px;
  text-transform: uppercase;
  font-weight: bold;
  padding: 5px;
  color: #44B25C;
}

/****************************************************************************/
/* BASIC FORM INPUTS */
/****************************************************************************/
.c-input {
  display: block;
  width: 100%;
  outline: 0;
}

.c-input::-webkit-input-placeholder {
  color: #8b8a89;
}

.c-input::-moz-placeholder {
  color: #8b8a89;
}

.c-input:-moz-placeholder {
  color: #8b8a89;
}

.c-input:-ms-input-placeholder {
  color: #8b8a89;
}

/**
* Default
*/
.c-input_default {
  border: 1px solid #d4d5d8;
  background: #fff;
  transition: background-color 0.3s, border-color 0.3s;
}

.c-input_default:focus {
  border-color: #288bd7;
}

.c-input_default_error {
  background-color: #f87f6e;
  border-color: #C3200A;
}

.c-input_default_success {
  background-color: #afe0ba;
  border-color: #44B25C;
}

/**
* Borderless
*/
.c-input_no-border {
  border-color: transparent !important;
}

.c-input_no-border:focus {
  border-color: transparent !important;
}

/**
* Text
*/
.c-input_text {
  background: none !important;
  padding: 0 !important;
  font-size: inherit !important;
  text-align: inherit !important;
  color: inherit !important;
}

/**
* Multiline input
*/
.c-input_multiline.c-input_xs {
  height: 70px;
  padding: 10px;
}

.c-input_multiline.c-input_sm {
  height: 85px;
  padding: 10px;
}

.c-input_multiline.c-input_md {
  height: 100px;
  padding: 10px;
}

.c-input_multiline.c-input_lg {
  height: 115px;
  padding: 10px 20px;
}

/**
* Extra small
*/
.c-input_xs {
  height: 24px;
  font-size: 14px;
  padding: 0 10px;
}

/**
* Small
*/
.c-input_sm {
  height: 30px;
  font-size: 14px;
  padding: 0 10px;
}

/**
* Medium
*/
.c-input_md {
  height: 34px;
  font-size: 16px;
  padding: 0 10px;
}

/* Large */
.c-input_lg {
  height: 44px;
  font-size: 18px;
  padding: 0 20px;
}

/**
* Label
*/
.c-input-label {
  display: inline-block;
  color: #24272E;
  font-weight: normal;
  margin-bottom: 8px;
}

/**
* Hidden
*/
.c-input_phone {
  border: 0 !important;
  clip: rect(0 0 0 0) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  white-space: nowrap !important;
  width: 1px !important;
}

/**
* Input groups
*/
.c-input-group {
  position: relative;
  white-space: nowrap;
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
  align-items: stretch;
}

.c-input-group__input {
  flex: 1 1 auto;
  width: 1%;
}

/****************************************************************************/
/* SELECT */
/****************************************************************************/
.c-select {
  position: relative;
  display: inline-block;
}

.c-select__btn {
  width: 25px;
  position: absolute;
  z-index: 1;
  right: 0;
  top: 0;
  bottom: 0;
  pointer-events: none;
  background: #288bd7;
}

.c-select__icon {
  fill: #fff;
  width: 100%;
  height: 100%;
  padding: 11px;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  max-height: 100%;
  max-width: 100%;
}

.c-select__control {
  appearance: none;
  outline: 0;
  width: 100%;
}

.c-select__control::-ms-expand {
  display: none;
}

/**
* Single
*/
.c-select_single .c-select__control {
  padding-right: 45px;
}

.c-select_single.c-select_md .c-select__btn {
  width: 34px;
}

.c-select_multiple .c-select__btn {
  display: none;
}

/**
* UTILITIES
*/
/****************************************************************************/
/* CLEARFIX */
/****************************************************************************/
.u-clearfix:after {
  content: '';
  display: table;
  clear: both;
}

/****************************************************************************/
/* COLOR */
/****************************************************************************/
.u-color-text {
  color: #24272E !important;
}

.u-color-success {
  color: #44B25C !important;
}

.u-color-error {
  color: #C3200A !important;
}

.u-color-active-gray {
  color: #99A1AD !important;
}

.u-justify-space-between {
  justify-content: space-between !important;
}

.u-justify-center {
  justify-content: center !important;
}

.u-justify-end {
  justify-content: flex-end !important;
}

.u-align-center {
  align-items: center !important;
}

.u-align-end {
  align-items: flex-end !important;
}

.u-flex-grow-0 {
  flex-grow: 0 !important;
}

.u-flex-grow-1 {
  flex-grow: 1 !important;
}

.u-flex-shrink-0 {
  flex-grow: 0 !important;
}

.u-flex-shrink-1 {
  flex-grow: 1 !important;
}

/****************************************************************************/
/* TEXT ALIGN */
/****************************************************************************/
.u-text-right {
  text-align: right !important;
}

.u-text-left {
  text-align: left !important;
}

.u-text-center {
  text-align: center !important;
}

/****************************************************************************/
/* VISIBILITY */
/****************************************************************************/
/**
* Hide visually and from screen readers.
*/
.u-hidden {
  display: none !important;
}

/**
* Hide only visually, but have it available for screen readers:
* http://snook.ca/archives/html_and_css/hiding-content-for-accessibility
*/
.u-hidden-visually {
  border: 0 !important;
  clip: rect(0 0 0 0) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  white-space: nowrap !important;
  width: 1px !important;
}

.u-centered {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  max-height: 100%;
  max-width: 100%;
}

/****************************************************************************/
/* GRID */
/****************************************************************************/
.u-col-1-12 {
  width: 8.33333% !important;
}

.u-col-2-12 {
  width: 16.66667% !important;
}

.u-col-3-12 {
  width: 25% !important;
}

.u-col-4-12 {
  width: 33.33333% !important;
}

.u-col-5-12 {
  width: 41.66667% !important;
}

.u-col-6-12 {
  width: 50% !important;
}

.u-col-7-12 {
  width: 58.33333% !important;
}

.u-col-8-12 {
  width: 66.66667% !important;
}

.u-col-9-12 {
  width: 75% !important;
}

.u-col-10-12 {
  width: 83.33333% !important;
}

.u-col-11-12 {
  width: 91.66667% !important;
}

.u-col-12-12 {
  width: 100% !important;
}

/**
* GENERAL CSS
*/
/****************************************************************************/
/* MAIN STYLESHEET */
/****************************************************************************/
.svg-ico {
  display: inline-block;
  vertical-align: middle;
  fill: currentColor;
  stroke: currentColor;
}

.svg-ico_no-fill {
  fill: none !important;
}

.svg-ico_no-stroke {
  stroke: none !important;
}

/**
* login block
*/
.login-block {
  width: 320px;
  height: 320px;
  display: inline-block;
  border: 8px solid #fff;
  border-radius: 16px;
  text-decoration: none;
}

.login-block:active {
  opacity: 0.9;
}

.login-block_error {
  border-color: #D11F1F;
}

.login-block_success {
  border-color: #14CC14;
}

.login-block_authorized {
  background: #fff;
  padding: 16px;
  color: #000;
  overflow: hidden;
}

.login-block_transparent {
  border-width: 0;
  position: relative;
}

.login-block_transparent .login-block__result {
  position: absolute;
  bottom: 100%;
  margin-bottom: 12px;
}

.login-block__result {
  height: 48px;
}

.login-block__thumb {
  height: 104px;
  width: 104px;
  margin: 0 auto 24px;
  position: relative;
  overflow: hidden;
}

.login-block__shortname {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.login-block__img {
  display: block;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 50%;
}

.login-block__title {
  font-weight: bold;
  font-size: 28px;
  line-height: 32px;
  letter-spacing: -.3px;
}

/**
* content
*/
.content__col {
  flex: 0 0 auto;
  max-width: 100%;
}

.content__col + .content__col {
  border-left: 1px solid rgba(0, 0, 0, 0.1);
}

.content__col.content__opened {
  border-left: 0;
}

.content__col > [id^="comp_"] {
  height: 100%;
}

.content__aside {
  width: 328px;
  padding: 0;
}

.content__kitchen {
  flex-basis: 266px;
}

.content__aside-kitchen {
  padding-left: 60px;
}

.content__opened,
.content__checkout {
  width: 268px;
}

.content__filler {
  display: none;
  flex: 1 1 0%;
  min-width: 0;
}

@media (min-width: 1200px) {
  .content__filler {
    display: block;
  }
}

.content__nav {
  width: 160px;
  margin-left: auto;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 10px;
  padding-right: 10px;
  position: relative;
  z-index: 2;
}

/**
* inline list
*/
.inline-list {
  display: flex;
  flex-wrap: wrap;
}

.inline-list__item {
  flex: 0 0 auto;
  max-width: 100%;
}

.inline-list__item + .inline-list__item:before {
  content: ' • ';
  display: inline-block;
  margin: 0 3px;
}

.order-btn {
  height: 40px;
  line-height: 38px;
  font-weight: 600;
}

.c-btn_default.order-btn_success {
  background-color: #44B25C;
  border-color: #44B25C;
}

.c-btn_default.order-btn_error {
  background-color: #C3200A;
  border-color: #C3200A;
}

.c-sup {
  color: rgba(36, 39, 46, 0.5);
  position: absolute;
  top: 0;
  margin-left: 2px;
}

/**
* Menu slider
*/
.keyboard .menu-slider {
  height: 100%;
  max-width: none;
  margin-left: -20px;
  margin-right: -20px;
}

.menu-slider {
  max-width: 100%;
  position: relative;
}

.menu-slider__main {
  width: calc(100% - 86px);
  height: 100%;
  overflow: hidden;
  padding-left: 20px;
  padding-right: 0;
  border-left: 1px solid rgba(255, 255, 255, 0.1);
}

.menu-slider__main .slick-list,
.menu-slider__main .slick-track {
  height: 100%;
}

.menu-slider__main .slick-slide {
  overflow-y: auto;
  padding-right: 20px;
}

.menu-slider__main .slick-slide.disable-overflow .simplebar-content {
  height: 100%;
  overflow: hidden;
}

.menu-slider__main .slick-slide > div {
  height: 100%;
}

.menu-slider__panel {
  overflow: hidden;
}

.menu-slider__aside {
  width: 86px;
  height: 100%;
  overflow: hidden;
  padding-left: 20px;
  padding-right: 20px;
}

.controls-list {
  margin-left: -6px;
  margin-right: -6px;
}

.controls-list__item {
  padding-left: 6px;
  padding-right: 6px;
}

.account-card__thumb {
  width: 72px;
  height: 72px;
  border-radius: 50%;
  overflow: hidden;
  margin-bottom: 12px;
  text-align: center;
  line-height: 72px;
  position: relative;
}

.account-card__img {
  display: block;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: rgba(37, 40, 44, 0.1);
}

.account-card__shortname {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  line-height: inherit;
}

.account-card__title {
  margin-bottom: 3px;
}

.c-panel .account-card,
.c-panel .shift-details {
  padding-left: 12px;
  padding-right: 12px;
}

.buttons-menu__item + .buttons-menu__item {
  margin-top: 8px;
}

.buttons-menu__link {
  border-radius: 10px;
  font-size: 17px;
  font-weight: bold;
  height: 54px;
  line-height: 52px;
}

.buttons-menu__link .c-btn__icon {
  width: 24px;
  height: 24px;
  margin-right: 5px;
}

.popup-notifications {
  position: absolute;
  bottom: 8px;
  left: 0;
  right: 0;
}

.popup-notifications .notice + .notice {
  margin-top: 8px;
}

.account-menu__item {
  padding-left: 12px;
  padding-right: 12px;
}

.account-menu__item:first-child {
  border-top: 0.5px solid rgba(0, 0, 0, 0.1);
}

.account-menu__item .account-menu__link {
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.1);
}

.account-menu__link {
  display: block;
  text-decoration: none;
  color: #24272E;
  position: relative;
  padding: 0 30px 0 12px;
  height: 58px;
  line-height: 58px;
}

.account-menu__icon {
  width: 10px;
  height: 10px;
  position: absolute;
  right: 20px;
  top: 50%;
  margin-top: -5px;
}

.c-panel .account-menu {
  margin-left: -12px;
  margin-right: -12px;
}

